export default {
	
	data() {
		return {
			currentIndex:0,
			currentGrape:"长相思",
			result: [],
			wine:[],
		}
	},
	created() {
		console.log("you in")
		const db = wx.cloud.database()
		const grape = db.collection("grape_category")
		const wines = db.collection("wines")
		grape.get()
			.then((res) => {
				console.log(res.data)
				this.result = res.data //you can define the name by uslef 
			})
			.catch((err) => {
				console.log(err)
			})
			
		wines.get()
			.then((res) => {
				console.log(res)
				this.wine = res.data //you can define the name by uslef 
				//vuex 传值
				let wineList =this.wine
				
				this.$store.dispatch('createWineList',wineList)
			})
			.catch((err) => {
				console.log(err)
			})
			
			
	},
	methods:{
		setCurrentState(i,g){
			
			this.currentIndex=i
			this.currentGrape=g
		},
		goToDetailPage(id){
			uni.navigateTo({
				url:"../wine_detail/wine_detail?id="+id,
				
				fail:(f)=>{
					console.log(f)
				},
			})
			
		}
	}
}
